<template>
  <div class="post">
    <div class="post-title">{{post.title}}</div>
    <p class="post-date">{{post.date}} --- {{post.filter}}</p>
    <hr size="1" />
    <component :is="component"></component>
  </div>
</template>

<script>
import Post1 from "../components/Post1";
import Post2 from "../components/Post2";
import Post3 from "../components/Post3";
import Post4 from "../components/Post4";
import Post5 from "../components/Post5";

export default {
  name: "post",
  components: {
    Post1,
    Post2,
    Post3,
    Post4,
    Post5
  },
  data() {
    return {
      post: {},
      component: ""
    };
  },
  mounted() {
    this.post = this.$store.state.allPosts[this.$route.params.post_id];
    this.component = `Post${this.$store.state.allPosts[
      this.$route.params.post_id
    ].id + 1}`;
  }
};
</script>

<style lang="scss">
.post {
  padding: 3em 5em;
  width: 70%;
  &-title {
    color: #333;
    font-size: 2.5em;
    text-align: center;
  }
  &-date {
    text-transform: uppercase;
    color: #969696;
    font-size: 0.9em;
    text-align: center;
    margin: 1em 0 2em 0;
  }
  hr {
    color: #d3d3d3;
    margin-bottom: 2em;
  }
}
</style>