import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import AllPosts from './views/AllPosts.vue'
import Post from './views/Post.vue'
import About from './views/About.vue'
import Contact from './views/Contact.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/all_posts',
      name: 'all-posts',
      component: AllPosts
    },
    {
      path: '/post/:post_id',
      name: 'post',
      component: Post
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    }
  ]
})
